body {
    background-color: #EEF2F8;
    height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    margin: 0;
    padding: 0;


}


html,
.body--container{
    height: 100%;
    /* background-color: red; */
}

.loader{
    display: flex;
    /* height: 100%; */
    width: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    /* background-color: red; */
    align-items: center;
    justify-content: center;
    align-items: flex-start;
}
.loader--text{
    color: #7D7F9B;
    font-size: 60px;
    font-weight: 100;
    font-style: italic;
}


.body--container{
    max-width: 1280px;
    margin: 60px auto;
    display: flex;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;

}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; 
}