

.movie--container{
    width:48%;
    background-color: #FFF;
    box-shadow: 0 2px 30px rgba(0,0,0,0.12);
    box-sizing: border-box;
    padding: 30px;
    margin-bottom: 60px;
    /* height: max-content; */
    cursor: pointer;
}

.movie--container:hover{
    box-shadow: 0 4px 40px rgba(106,106,247,0.2);
    /* width: 49%; */
    transition: .3s;
}

.movie--data{
    display: flex;
    /* position: absolute; */
}

.movie--poster{
    position: relative;
    top: -60px;
    /* top: -80px; */
    box-shadow: 0 2px 15px rgba(0,0,0,0.2);
}

.movie--information{
    margin-left: 23px;
}

.movie--title{
    margin-top: 26px;
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
    color: #1F2329;

}

.movie--year{
    font-size: 16px;
    color: #727272;
    font-weight: 400;
}

.movie--genres{
    display: flex;
    margin-top: 28px;
    margin-bottom: 43px;
    flex-wrap: wrap;
}

.movie--rating{
    color: #FFAA00;
    font-weight: 600;
    font-size: 20px;
}

.movie--genre{
    background-color: #F2F2F2;
    height: 24px;
    padding: 0 10px;
    color: #878787;
    border-radius: 12px;
    display: flex;
    width: max-content;
    align-content: center;
    align-items: center;
    font-size: 14px;
    margin-right: 6px;
    margin-bottom: 6px;
    

}

.movie--summary{
    font-size: 16px;
    line-height: 24px;
    color: #979797;
    
}